<template>
  <div class="we">
    <el-carousel :interval="5000" arrow="always" height="400px" class="animated fadeInDown delay_0">
        <el-carousel-item v-for="item in what" :key="item.p">
          <p>{{item.p}}</p>
          <h2>{{item.h2}}</h2>
          <h3>{{item.h3}}</h3>
        </el-carousel-item>
    </el-carousel>
    <div v-for="item in wes" :key="item.label" class="url-top">
        <h3 class="url-title">{{ item.label }}</h3>
        <div>
          <div class="url-box"  v-for="item1 in item.children" :key="item1.label">
            <a :href="item1.router" target="_blank">
              <p class="url-title-1">{{ item1.title }}</p>
              <div class="url-text">
                <p class="url-text-1">{{ item1.text }}</p>
              </div>
            </a>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import { wes } from "~/data/index";

export default {
  data() {
    return {
      wes: wes,
      what:[
        {
          p:'王安安',
          h2:'娜乌西卡科技有限公司-技术部',
          h3:'导航站的意义在于，给你分担寻找站点的苦难'
        },
        {
          p:'黄岳龙',
          h2:'娜乌西卡科技有限公司-金融部',
          h3:'代码的意义在于，你可以让已经很美的物体变得更美1'
        },
        {
          p:'廖旭凯',
          h2:'娜乌西卡科技有限公司-运营部',
          h3:'代码的意义在于，你可以让已经很美的物体变得更美2'
        }
      ]
    };
  },
  methods: {}
};
</script>

<style scoped lang="scss">
.el-carousel__item {
  color: #fff;
  text-align: left;
}

.el-carousel__item p {
  font-size: 17px;
  padding: 120px 50px 10px 100px;
  letter-spacing: 1px;
  font-weight: 300;
}

.el-carousel__item h2 {
  font-size: 42px;
  width: 740px;
  padding: 0 50px 10px 100px;
  letter-spacing: 1px;
}

.el-carousel__item h3 {
  font-size: 16px;
  width: 400px;
  padding: 0 50px 10px 100px;
  letter-spacing: 1px;
  line-height: 27px;
}

.el-carousel__item:nth-child(2n) {
  background-color: #000;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #000;
}

.url-top h3 {
  background: #000;
  color: #fff;
  margin: 20px 0;
  padding: 15px;
  padding-left: 50px;
  font-weight: 200;
  font-size: 28px;
}

.url-box {
  height: 40px;
  background: #fff;
  width: 19.2%;
  display: inline-block;
  margin-right: 1%;
  margin-bottom: 20px;
  transition: 0.4s;
  box-shadow: 0 4px 8px 0 rgba(7, 17, 27, 0.02);
  position: relative;
  float: left;
}

.url-box:nth-child(5n) {
  margin-right: 0;
}

.url-top:after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.url-title-1 {
  background: #fff;
  color: #000;
  line-height: 40px;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
}

.url-text {
  color: #000;
  width: 100%;
  height: 0;
  overflow: hidden;
  position: absolute;
  top: 120px;
  left: 0;
  background: #fff;
  transition: 0.3s;
  z-index: 100;
}

.url-text-1 {
  padding: 15px;
}

.url-box:hover .url-text {
  height: 180px;
  top: 40px;
  box-shadow: 0 8px 16px 0 rgba(7, 17, 27, 0.2);
}

.url-box:hover .url-title-1 {
  background: #000;
  transition: 0.2s;
  color: #fff;
}

.we {
  margin-bottom: 100px;
}
</style>



